* {
  box-sizing: border-box;
  padding: 0;
  margin: 0;
}

.App {
  display: flex;
  height: 100vh;
  background-color: skyblue;
  padding: 1rem;
  flex-direction: column;
  gap: 1rem;
}

.form {
  display: flex;
  flex-direction: column;
  gap: .5rem;
}

input {
  width: 100%;
  padding: 10px;
  background-color: white;
  outline: none;
  border: 1px solid black;
  border-radius: 10px;
}

.button-container {
  display: flex;
  gap: 1rem;
  justify-content: center;
}

button {
  outline: none;
  padding: 5px;
  border: none;
  border-radius: 5px;
}

.button {
  background-color: white;
}

.card {
  background-color: white;
  padding: 1rem;
  display: flex;
  justify-content: center;
  border-radius: 10px;
}

.syms {
  background-color: white;
  border-radius: 10px;
  height: 300px;
  overflow-y: scroll;
  padding: 1rem;
  display: flex;
  flex-direction: column;
  gap: .5rem;
}

ol {
  list-style: none;
}